.header-color{
    background: #141E30;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #243B55, #141E30);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #243B55, #141E30); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.landing-grid{
    background: #141E30;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #243B55, #141E30);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #243B55, #141E30); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    text-align: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}


.avatar-img{
    height: 200px;
    padding-top: 10em;
    margin: 20px;

}

.banner-text{
    background-color: black;
    opacity: .8;
    width: 75%;
    margin: auto;
    border-radius: 10px;
}

.banner-text h1{
    font-size: 66px;
    font-weight: bold;
    color: white;

}
.banner-text hr{
    border-top: 5px dotted white;
    width: 50%;
    margin: auto;
}

.banner-text p{
    color: white;
    font-size: 20px;
    padding: 10px;
}

.social-links{
    display: flex;
    justify-content: space-between;
    padding-left: 18em;
    width: 50%;
    
}

.social-links i{
    color: white;
    font-size: 5em;
    padding: 8px;
}

/* Projects page CSS*/
.projects-grid {
    display: flex;
}

/* Contact page CSS */

.contact-body {
    margin: auto;
    position: fixed;
    width: 100%;
    height: 100%;
    justify-content: center;
    padding-top: 5em;
}

.contact-grid {
    text-align: center;
    justify-content: center;
    width: 80%;
    height: 550px;
    background: white;
    color: black;
    margin-bottom: 20px;

}

.contact-grid h2{
    font-family: 'Montserrat', sans-serif;
    
}

.contact-grid p{
    font-family: 'Raleway', sans-serif;
    font-weight: bold;
}

.contact-grid hr{
    border-top: 10px dotted black;
    width: 50%;
    margin: auto;
}

.contact-list i{
    font-size: 30px;
    padding-right: 2em;
}

.example{
    position:absolute !important;
    }